body {
  margin: 0;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Poppins, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}


body.light-mode {
  transition: background-color 1.0s ease-in-out, color 1.0s;
  background-color: white;
  color: black;
}

body.light-mode a {
  transition: background-color 1.0s ease-in-out, color 1.0s;
  color: black;
}

body.dark-mode {
  transition: background-color 1.0s ease-in-out, color 1.0s;
  background-color: black;
  color: white;
}

body.dark-mode a {
  transition: background-color 1.0s ease-in-out, color 1.0s;
  color: white;
}


body.dark-mode h1 {
  transition: background-color 1.0s ease-in-out, color 1.0s;
  color: white;
}

a {
  text-decoration: none;
  font-size: 23px;
}

a:hover {
  text-decoration: underline;
  text-underline-offset: 0.6rem;
  text-decoration-thickness: 2.6px;
  opacity: 0.5;
  transition: text-decoration 1s ease-out 1s, opacity 1s ease-out  0.3s;
}

p {
  font-size: 3rem;
  font-weight: 500;
}