.projects-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 0 1rem;
    margin: 0 10px;
    text-align: center;
    width: 97%;
    overflow: hidden;
    transition: all 0.3s ease;
}

.projects-container > p {
    font-size: 3rem;
    font-weight: bold;
    padding-bottom: 3rem;
    text-align: center;
}

.cards {
    padding-top: 5%;
    display: flex;
    align-items: center;
    gap: 50px;
    height: 300px;
    position: relative;
    animation: slide 10s linear infinite;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 400px;
    height: 400px;
    border: 2px solid;
    padding: 10px 6px;
    border-radius: 10px;
    text-align: center;
    transition: transform 0.3s ease-in-out;
}

.card:hover {
    transform: scale(1.05) translateY(-10px);
}

.card > img {
    width: 100px;
    height: 100px;
    padding: 10px 6px;
    object-fit: contain;
    border-radius: 8px;
}

.card > h6 {
    font-size: 1.5rem;
    margin: 10px 0;
}

.card > p {
    font-size: 1rem;
    margin: 10px 15px;
}

.btn-container > button {
    background: none;
    cursor: pointer;
    padding: 15px 10px;
    border: none;
    margin: 1rem 0;
    transition: all 0.3s ease;
}

.btn-container > button > a:hover {
    transform: scale(1.1);
}

.btn-container > button > a {
    height: 100%;
    width: 100%;
    padding: 10px 15px;
    border: 2px solid;
    border-radius: 50px;
    text-decoration: none;
    margin: 10px 10px;
}

@keyframes slide {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(calc(-380px * 4));
    }
}

.cards:hover {
    animation-play-state: paused;
}


.btn-dark {
    background-color: black;
}
  
.btn-dark:hover {
    background-color: white;
    color: black;
    transform: scale(1.1);
}
  
.btn-light {
    background-color: white;
    color: black;
}
  
.btn-light:hover {
    background-color: black;
    color: white;
    transform: scale(1.1);
}
  